<template>
	<view class="home-page">
		<!-- È°∂ÈÉ®Ê¨¢ËøéÂå∫Âüü -->
		<view class="welcome-section">
			<view class="welcome-content">
				<text class="welcome-title">ÊôãÊ±üÊï∞ÊçÆ‰∏≠ÂøÉ</text>
				<text class="welcome-subtitle">ÂÆûÊó∂ÊéåÊè°ÊúÄÊñ∞Ê¶úÂçïÂä®ÊÄÅ</text>
			</view>
			<view class="refresh-btn" @tap="refreshData">
				<text class="refresh-text">Âà∑Êñ∞</text>
			</view>
		</view>
		
		<!-- Ê†∏ÂøÉÁªüËÆ°Êï∞ÊçÆ -->
		<view class="stats-section">
			<text class="section-title">Êï∞ÊçÆÊ¶ÇËßà</text>
			<view class="stats-grid">
				<StatsCard 
					v-for="stat in coreStats" 
					:key="stat.key"
					:title="stat.title"
					:value="stat.value"
					:trend="stat.trend"
					:icon="stat.icon"
					:color="stat.color"
					@click="onStatClick(stat)"
				/>
			</view>
		</view>
		
		<!-- ÂàÜÁ´ôÊï∞ÊçÆÁªüËÆ° -->
		<view class="sites-section">
			<view class="section-header">
				<text class="section-title">ÂàÜÁ´ôÁªüËÆ°</text>
				<text class="section-more" @tap="goToRanking">Êü•ÁúãÊõ¥Â§ö</text>
			</view>
			<view class="sites-grid">
				<view 
					class="site-card" 
					v-for="site in siteStats" 
					:key="site.key"
					@tap="goToSite(site)"
				>
					<view class="site-info">
						<text class="site-name">{{ site.name }}</text>
						<text class="site-count">{{ site.rankingCount }}‰∏™Ê¶úÂçï</text>
					</view>
					<view class="site-trend">
						<text class="trend-value" :class="site.trend > 0 ? 'trend-up' : 'trend-down'">
							{{ site.trend > 0 ? '+' : '' }}{{ site.trend }}
						</text>
						<text class="trend-label">Êú¨Âë®</text>
					</view>
				</view>
			</view>
		</view>
		
		<!-- ÁÉ≠Èó®Ê¶úÂçï -->
		<view class="hot-rankings-section">
			<view class="section-header">
				<text class="section-title">ÁÉ≠Èó®Ê¶úÂçï</text>
				<text class="section-more" @tap="goToRanking">Êü•ÁúãÂÖ®ÈÉ®</text>
			</view>
			<scroll-view class="rankings-scroll" scroll-x>
				<view class="rankings-list">
					<view 
						class="ranking-item" 
						v-for="ranking in hotRankings" 
						:key="ranking.id"
						@tap="goToRankingDetail(ranking)"
					>
						<view class="ranking-badge" v-if="ranking.isHot">HOT</view>
						<text class="ranking-name">{{ ranking.name }}</text>
						<text class="ranking-desc">{{ ranking.bookCount }}Êú¨‰π¶Á±ç</text>
						<text class="ranking-update">{{ formatTime(ranking.updateTime) }}</text>
					</view>
				</view>
			</scroll-view>
		</view>
		
		<!-- ÊúÄËøëÊõ¥Êñ∞ -->
		<view class="recent-section">
			<text class="section-title">ÊúÄËøëÊõ¥Êñ∞</text>
			<view class="recent-list">
				<view 
					class="recent-item" 
					v-for="item in recentUpdates" 
					:key="item.id"
					@tap="goToDetail(item)"
				>
					<view class="recent-info">
						<text class="recent-title">{{ item.title }}</text>
						<text class="recent-subtitle">{{ item.subtitle }}</text>
					</view>
					<view class="recent-time">
						<text class="time-text">{{ formatTime(item.updateTime) }}</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import StatsCard from '@/components/StatsCard.vue'
import { get } from '@/utils/request.js'
import { getSync, setSync } from '@/utils/storage.js'

/**
 * È¶ñÈ°µÁªÑ‰ª∂
 * @description Â±ïÁ§∫ÁªüËÆ°Êï∞ÊçÆÊ¶ÇËßà„ÄÅÁÉ≠Èó®Ê¶úÂçï„ÄÅÊúÄËøëÊõ¥Êñ∞Á≠â‰ø°ÊÅØ
 */
export default {
	name: 'HomePage',
	components: {
		StatsCard
	},
	
	data() {
		return {
			// Ê†∏ÂøÉÁªüËÆ°Êï∞ÊçÆ
			coreStats: [
				{
					key: 'totalBooks',
					title: 'ÊÄª‰π¶Á±çÊï∞',
					value: 0,
					trend: 0,
					icon: 'üìö',
					color: '#007AFF'
				},
				{
					key: 'totalRankings',
					title: 'ÊÄªÊ¶úÂçïÊï∞',
					value: 0,
					trend: 0,
					icon: 'ÔøΩÔøΩ',
					color: '#34C759'
				},
				{
					key: 'todayUpdates',
					title: '‰ªäÊó•Êõ¥Êñ∞',
					value: 0,
					trend: 0,
					icon: 'üîÑ',
					color: '#FF9500'
				},
				{
					key: 'activeUsers',
					title: 'Ê¥ªË∑ÉÁî®Êà∑',
					value: 0,
					trend: 0,
					icon: 'üë•',
					color: '#AF52DE'
				}
			],
			
			// ÂàÜÁ´ôÁªüËÆ°Êï∞ÊçÆ
			siteStats: [],
			
			// ÁÉ≠Èó®Ê¶úÂçï
			hotRankings: [],
			
			// ÊúÄËøëÊõ¥Êñ∞
			recentUpdates: [],
			
			// Âä†ËΩΩÁä∂ÊÄÅ
			loading: false,
			
			// Âà∑Êñ∞Áä∂ÊÄÅ
			refreshing: false
		}
	},
	
	onLoad() {
		this.initData()
	},
	
	onShow() {
		// ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅÂà∑Êñ∞Êï∞ÊçÆ
		this.checkDataFreshness()
	},
	
	// ‰∏ãÊãâÂà∑Êñ∞
	onPullDownRefresh() {
		this.refreshData().finally(() => {
			uni.stopPullDownRefresh()
		})
	},
	
	methods: {
		/**
		 * ÂàùÂßãÂåñÊï∞ÊçÆ
		 */
		async initData() {
			this.loading = true
			
			try {
				// ÂÖàÂä†ËΩΩÁºìÂ≠òÊï∞ÊçÆ
				this.loadCachedData()
				
				// ÂÜçËé∑ÂèñÊúÄÊñ∞Êï∞ÊçÆ
				await this.fetchData()
			} catch (error) {
				console.error('ÂàùÂßãÂåñÊï∞ÊçÆÂ§±Ë¥•:', error)
				this.showError('Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•')
			} finally {
				this.loading = false
			}
		},
		
		/**
		 * Âä†ËΩΩÁºìÂ≠òÊï∞ÊçÆ
		 */
		loadCachedData() {
			const cachedStats = getSync('homeStats')
			const cachedSiteStats = getSync('homeSiteStats')
			const cachedHotRankings = getSync('homeHotRankings')
			const cachedRecentUpdates = getSync('homeRecentUpdates')
			
			if (cachedStats) {
				this.updateCoreStats(cachedStats)
			}
			
			if (cachedSiteStats) {
				this.siteStats = cachedSiteStats
			}
			
			if (cachedHotRankings) {
				this.hotRankings = cachedHotRankings
			}
			
			if (cachedRecentUpdates) {
				this.recentUpdates = cachedRecentUpdates
			}
		},
		
		/**
		 * Ëé∑ÂèñÊúÄÊñ∞Êï∞ÊçÆ
		 */
		async fetchData() {
			try {
				// Âπ∂Ë°åËØ∑Ê±ÇÊâÄÊúâÊï∞ÊçÆ
				const [statsRes, sitesRes, rankingsRes, updatesRes] = await Promise.all([
					get('/api/stats/overview'),
					get('/api/sites/stats'),
					get('/api/rankings/hot', { limit: 6 }),
					get('/api/recent/updates', { limit: 8 })
				])
				
				// Êõ¥Êñ∞Ê†∏ÂøÉÁªüËÆ°
				if (statsRes) {
					this.updateCoreStats(statsRes)
					setSync('homeStats', statsRes, 5 * 60 * 1000) // ÁºìÂ≠ò5ÂàÜÈíü
				}
				
				// Êõ¥Êñ∞ÂàÜÁ´ôÁªüËÆ°
				if (sitesRes) {
					this.siteStats = sitesRes
					setSync('homeSiteStats', sitesRes, 10 * 60 * 1000) // ÁºìÂ≠ò10ÂàÜÈíü
				}
				
				// Êõ¥Êñ∞ÁÉ≠Èó®Ê¶úÂçï
				if (rankingsRes) {
					this.hotRankings = rankingsRes
					setSync('homeHotRankings', rankingsRes, 15 * 60 * 1000) // ÁºìÂ≠ò15ÂàÜÈíü
				}
				
				// Êõ¥Êñ∞ÊúÄËøëÊõ¥Êñ∞
				if (updatesRes) {
					this.recentUpdates = updatesRes
					setSync('homeRecentUpdates', updatesRes, 5 * 60 * 1000) // ÁºìÂ≠ò5ÂàÜÈíü
				}
				
			} catch (error) {
				console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', error)
				throw error
			}
		},
		
		/**
		 * Êõ¥Êñ∞Ê†∏ÂøÉÁªüËÆ°Êï∞ÊçÆ
		 */
		updateCoreStats(data) {
			this.coreStats.forEach(stat => {
				if (data[stat.key] !== undefined) {
					stat.value = data[stat.key]
					stat.trend = data[`${stat.key}Trend`] || 0
				}
			})
		},
		
		/**
		 * Ê£ÄÊü•Êï∞ÊçÆÊñ∞È≤úÂ∫¶
		 */
		checkDataFreshness() {
			const lastUpdate = getSync('homeLastUpdate', 0)
			const now = Date.now()
			
			// Â¶ÇÊûúË∂ÖËøá10ÂàÜÈíüÔºåÈùôÈªòÂà∑Êñ∞Êï∞ÊçÆ
			if (now - lastUpdate > 10 * 60 * 1000) {
				this.fetchData().catch(() => {
					// ÈùôÈªòÂ§±Ë¥•
				}).finally(() => {
					setSync('homeLastUpdate', now)
				})
			}
		},
		
		/**
		 * Âà∑Êñ∞Êï∞ÊçÆ
		 */
		async refreshData() {
			if (this.refreshing) return
			
			this.refreshing = true
			
			try {
				await this.fetchData()
				setSync('homeLastUpdate', Date.now())
				
				uni.showToast({
					title: 'Âà∑Êñ∞ÊàêÂäü',
					icon: 'success',
					duration: 1500
				})
			} catch (error) {
				this.showError('Âà∑Êñ∞Â§±Ë¥•')
			} finally {
				this.refreshing = false
			}
		},
		
		/**
		 * Ê†ºÂºèÂåñÊó∂Èó¥ÊòæÁ§∫
		 */
		formatTime(time) {
			if (!time) return 'Êú™Áü•'
			
			const now = new Date()
			const updateTime = new Date(time)
			const diff = now - updateTime
			
			const minutes = Math.floor(diff / (1000 * 60))
			const hours = Math.floor(diff / (1000 * 60 * 60))
			const days = Math.floor(diff / (1000 * 60 * 60 * 24))
			
			if (minutes < 60) {
				return `${minutes}ÂàÜÈíüÂâç`
			} else if (hours < 24) {
				return `${hours}Â∞èÊó∂Ââç`
			} else if (days < 7) {
				return `${days}Â§©Ââç`
			} else {
				return updateTime.toLocaleDateString()
			}
		},
		
		/**
		 * ÊòæÁ§∫ÈîôËØØÊèêÁ§∫
		 */
		showError(message) {
			uni.showToast({
				title: message,
				icon: 'none',
				duration: 2000
			})
		},
		
		/**
		 * ÁªüËÆ°Âç°ÁâáÁÇπÂáª‰∫ã‰ª∂
		 */
		onStatClick(stat) {
			console.log('ÁÇπÂáªÁªüËÆ°Âç°Áâá:', stat)
			// ÂèØ‰ª•Ë∑≥ËΩ¨Âà∞ÂØπÂ∫îÁöÑËØ¶ÊÉÖÈ°µÈù¢
		},
		
		/**
		 * Ë∑≥ËΩ¨Âà∞Ê¶úÂçïÈ°µÈù¢
		 */
		goToRanking() {
			uni.switchTab({
				url: '/pages/ranking/index'
			})
		},
		
		/**
		 * Ë∑≥ËΩ¨Âà∞ÂàÜÁ´ôÈ°µÈù¢
		 */
		goToSite(site) {
			uni.switchTab({
				url: `/pages/ranking/index?site=${site.key}`
			})
		},
		
		/**
		 * Ë∑≥ËΩ¨Âà∞Ê¶úÂçïËØ¶ÊÉÖ
		 */
		goToRankingDetail(ranking) {
			uni.navigateTo({
				url: `/pages/ranking/detail?id=${ranking.id}`
			})
		},
		
		/**
		 * Ë∑≥ËΩ¨Âà∞ËØ¶ÊÉÖÈ°µÈù¢
		 */
		goToDetail(item) {
			if (item.type === 'ranking') {
				this.goToRankingDetail(item)
			} else if (item.type === 'book') {
				uni.navigateTo({
					url: `/pages/book/detail?id=${item.id}`
				})
			}
		}
	}
}
</script>

<style lang="scss" scoped>
.home-page {
	min-height: 100vh;
	background-color: $page-background;
	padding-bottom: $safe-area-bottom;
}

.welcome-section {
	@include flex-between;
	align-items: center;
	padding: $spacing-lg;
	background: linear-gradient(135deg, $primary-color, $secondary-color);
	color: white;
	
	.welcome-content {
		flex: 1;
		
		.welcome-title {
			display: block;
			font-size: $font-size-xl;
			font-weight: bold;
			margin-bottom: $spacing-xs;
		}
		
		.welcome-subtitle {
			font-size: $font-size-sm;
			opacity: 0.9;
		}
	}
	
	.refresh-btn {
		@include flex-center;
		padding: $spacing-xs $spacing-md;
		background-color: rgba(255, 255, 255, 0.2);
		border-radius: $border-radius-medium;
		
		.refresh-text {
			color: white;
			font-size: $font-size-sm;
		}
		
		&:active {
			opacity: 0.7;
		}
	}
}

.stats-section {
	padding: $spacing-lg;
	
	.stats-grid {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: $spacing-md;
		margin-top: $spacing-md;
	}
}

.sites-section {
	padding: 0 $spacing-lg $spacing-lg;
	
	.sites-grid {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: $spacing-sm;
		margin-top: $spacing-md;
	}
	
	.site-card {
		@include card-style;
		@include flex-between;
		align-items: center;
		padding: $spacing-md;
		transition: all 0.3s ease;
		
		&:active {
			transform: scale(0.98);
		}
	}
	
	.site-info {
		flex: 1;
		
		.site-name {
			display: block;
			font-size: $font-size-md;
			font-weight: bold;
			color: $text-primary;
			margin-bottom: 4rpx;
		}
		
		.site-count {
			font-size: $font-size-xs;
			color: $text-secondary;
		}
	}
	
	.site-trend {
		@include flex-column-center;
		
		.trend-value {
			font-size: $font-size-sm;
			font-weight: bold;
			margin-bottom: 2rpx;
			
			&.trend-up {
				color: #34C759;
			}
			
			&.trend-down {
				color: #FF3B30;
			}
		}
		
		.trend-label {
			font-size: $font-size-xs;
			color: $text-placeholder;
		}
	}
}

.hot-rankings-section {
	padding: 0 $spacing-lg $spacing-lg;
	
	.rankings-scroll {
		margin-top: $spacing-md;
		white-space: nowrap;
	}
	
	.rankings-list {
		@include flex-center;
		gap: $spacing-sm;
		padding-bottom: $spacing-xs;
	}
	
	.ranking-item {
		@include card-style;
		position: relative;
		padding: $spacing-md;
		min-width: 200rpx;
		transition: all 0.3s ease;
		
		&:active {
			transform: scale(0.98);
		}
		
		.ranking-badge {
			position: absolute;
			top: -8rpx;
			right: -8rpx;
			background-color: $accent-color;
			color: white;
			font-size: $font-size-xs;
			padding: 2rpx 8rpx;
			border-radius: $border-radius-small;
		}
		
		.ranking-name {
			display: block;
			font-size: $font-size-md;
			font-weight: bold;
			color: $text-primary;
			margin-bottom: $spacing-xs;
			@include text-ellipsis;
		}
		
		.ranking-desc {
			display: block;
			font-size: $font-size-sm;
			color: $text-secondary;
			margin-bottom: 4rpx;
		}
		
		.ranking-update {
			font-size: $font-size-xs;
			color: $text-placeholder;
		}
	}
}

.recent-section {
	padding: 0 $spacing-lg $spacing-lg;
	
	.recent-list {
		margin-top: $spacing-md;
	}
	
	.recent-item {
		@include flex-between;
		align-items: center;
		padding: $spacing-md;
		background-color: white;
		border-radius: $border-radius-medium;
		margin-bottom: $spacing-sm;
		transition: all 0.3s ease;
		
		&:active {
			background-color: $background-color;
		}
		
		&:last-child {
			margin-bottom: 0;
		}
	}
	
	.recent-info {
		flex: 1;
		
		.recent-title {
			display: block;
			font-size: $font-size-md;
			color: $text-primary;
			margin-bottom: 4rpx;
			@include text-ellipsis;
		}
		
		.recent-subtitle {
			font-size: $font-size-sm;
			color: $text-secondary;
			@include text-ellipsis;
		}
	}
	
	.recent-time {
		margin-left: $spacing-sm;
		
		.time-text {
			font-size: $font-size-xs;
			color: $text-placeholder;
		}
	}
}

.section-title {
	font-size: $font-size-lg;
	font-weight: bold;
	color: $text-primary;
}

.section-header {
	@include flex-between;
	align-items: center;
	margin-bottom: $spacing-md;
	
	.section-more {
		font-size: $font-size-sm;
		color: $primary-color;
		
		&:active {
			opacity: 0.7;
		}
	}
}
</style>
